@use "sass:map";

/* Font Sizes */
$base-font-size: 16px; // Define the base font size first

$font-size-xs: $base-font-size * 0.5;
$font-size-sm: $base-font-size * 0.7;
$font-size-md: $base-font-size;
$font-size-lg: $base-font-size * 1.1;
$font-size-xl: $base-font-size * 1.5;

$border-radius: 3px;

/* Colours */
$blue: #007bff;
$indigo: #6610f2;
$purple: #6f42c1;
$pink: #e83e8c;
$red: #be4646;
$orange: #fd7e14;
$yellow: #ffc107;
$green: #28a745;
$teal: #20c997;
$cyan: #17a2b8;
$white: #fff;
$gray: #6c757d;
$gray-dark: #343a40;
$black: #191919;

$primary: #84B026;
$secondary: #6c757d;

$success: #28a745;
$info: #17a2b8;
$warning: #ffc107;
$danger: #dc3545;

$light: #f8f9fa;
$dark: #343a40;